import { useFeature } from 'feature-provider';
import PropTypes from 'prop-types';
import React, { useCallback, useEffect, useReducer, useState } from 'react';
import { version } from '../../../package.json';
import fetchTweetstorm from '../../api/fetchTweetstorm';
import Footer from '../../components/Footer/Footer';
import Form from '../../components/Form/Form';
import Loading from '../../components/Loading/Loading';
import LogIn from '../../components/LogIn/LogIn';
import NavBar from '../../components/NavBar/NavBar';
import ToastNotification from '../../components/ToastNotification/ToastNotification';
import useLocalStorage from '../../hooks/useLocalStorage';
import makeTweetstorm from '../../store/makeTweetstorm';
import { types } from '../../store/makeReducer';
import { DANGER, SUCCESS } from '../../constants';
import { useAuth0 } from '../../react-auth0-wrapper';
import fetchHealthAndSetHealthy from './fetchHealthAndSetHealthy';
import makeLink from './makeLink';
import SubHeader from '../../components/SubHeader/SubHeader';

function App({ initialState, reducer }) {
  const {
    isAuthenticated,
    loading,
    loginWithRedirect,
    logout,
    user
  } = useAuth0();

  const feature = useFeature();

  useEffect(() => {
    fetchHealthAndSetHealthy(dispatch);
  }, []);

  useEffect(() => {
    if (isAuthenticated && user) {
      dispatch({ type: types.SET_USER_ID, value: user.sub });
    }
  }, [isAuthenticated, user]);

  const [source_, setSource] = useLocalStorage('source', '');

  const [
    { healthy, items, notification, source, userId },
    dispatch
  ] = useReducer(reducer, {
    ...initialState,
    items: makeTweetstorm(feature)({
      linefeed: initialState.linefeed,
      source: source_
    }),
    source: source_
  });

  // eslint-disable-next-line consistent-return
  useEffect(() => {
    if (notification && notification.type !== DANGER) {
      const timer = setTimeout(() => {
        dispatch({ type: types.DISMISS_TOAST });
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [notification]);

  // TODO: add to global state?
  const [inReplyToTweetUrl, setInReplyToTweetUrl] = useState('');
  const [waiting, setWaiting] = useState(false);

  function resetTweetstorm(error, response) {
    let link = null;
    let message = null;
    let type = SUCCESS;
    if (error || response.error) {
      message = error ? error.message : response.error.message;
      type = DANGER;
    } else {
      link = makeLink(response.data);
    }
    console.log(error, response);
    dispatch({
      type: types.RESET_TWEETSTORM,
      value: {
        link,
        message,
        type
      }
    });
    if (type === SUCCESS) {
      setInReplyToTweetUrl('');
      setSource('');
    }
    setWaiting(false);
  }

  async function onClick() {
    setWaiting(true);
    try {
      const response = await fetchTweetstorm({
        inReplyToTweetUrl,
        items,
        userId
      });
      resetTweetstorm(null, response);
    } catch (error) {
      resetTweetstorm(error);
    }
  }

  const memoizedCallback = useCallback((error, data) => {
    if (data) {
      dispatch({
        type: types.APPEND_SCREEN_NAME,
        value: data.user.screen_name
      });
    }
  }, []);

  return loading ? (
    <Loading />
  ) : (
    <article className="container content-center mx-auto m-1 p-4">
      <header className="flex flex-row justify-between">
        {isAuthenticated ? (
          <NavBar logout={logout} user={user} />
        ) : (
          <span className="my-4" />
        )}
      </header>
      <SubHeader />
      {isAuthenticated ? (
        <Form
          healthy={healthy}
          inReplyToTweetUrl={inReplyToTweetUrl}
          items={items}
          memoizedCallback={memoizedCallback}
          onChangeSource={(e) => {
            dispatch({
              type: types.CHANGE_SOURCE,
              value: e.target.value
            });
            setSource(e.target.value);
          }}
          onChangeTweet={(e, item) => {
            dispatch({
              type: types.CHANGE_TWEET,
              value: {
                id: item.id,
                tweet: e.target.value
              }
            });
          }}
          onClick={onClick}
          setInReplyToTweetUrl={setInReplyToTweetUrl}
          source={source}
          userId={userId}
          waiting={waiting}
        />
      ) : (
        <LogIn loginWithRedirect={loginWithRedirect} />
      )}
      <ToastNotification
        notification={notification}
        onClick={() => dispatch({ type: types.DISMISS_TOAST })}
      />
      <Footer healthy={healthy} version={version} />
    </article>
  );
}

App.propTypes = {
  initialState: PropTypes.object.isRequired,
  reducer: PropTypes.func.isRequired
};

export default App;
